.hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.cat-thomb {
  gap: 20px;
  display: flex;
}

.bold_font {
  font-weight: bold;
}

.loader {
  width: 32px;
  height: 32px;
  color: #ff3d00;
  border-radius: 50%;
  animation: fill 1s ease-in infinite alternate;
  position: relative;
}

.loader:before, .loader:after {
  content: "";
  height: 100%;
  width: 100%;
  border-radius: 50%;
  animation: fill .9s ease-in infinite alternate;
  position: absolute;
  top: 0;
  left: 48px;
}

.loader:after {
  animation-duration: 1.1s;
  left: auto;
  right: 48px;
}

@keyframes fill {
  0% {
    box-shadow: inset 0 0 0 2px;
  }

  100% {
    box-shadow: inset 0 0 0 10px;
  }
}

